body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.cube-active {
  background: #FF0000;
  width: 9px;
  height: 9px;
  display: block;
  float: left;
}

h1 {
  font-family: 'Raleway Black';
  font-weight: 900;
  font-size: 60px;
  line-height: 110.4%;
  color: #303030;
  width: 362px;
  margin-top: 77px;
}

header {
  display: flex;
  width: 100%;
}

.menu {
  display: flex;
}

.link-menu {
  display: flex;
  padding-left: 70px;
  align-items: center;
}

.link-item {
  margin-right: 32px;
}

.cube-active__menu {
  margin-right: 14px;
  margin-top: 4px;
}

.logo {
  width: 82px;
  height: 82px;
  background: #3F3F3F;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapp {
  margin-left: 140px;
}

.slogan {
  font-family: 'Raleway Regular';
  font-size: 20px;
  line-height: 23px;
  margin-top: 53px;
}

.btn {
  width: 150px;
  height: 54px;
  background: #FF0000;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  text-decoration: none;
}